import React from 'react'
import { StaticQuery, graphql } from 'gatsby'
import Image from 'gatsby-image'

import { rhythm } from '../utils/typography'

function Bio() {
  return (
    <StaticQuery
      query={bioQuery}
      render={data => {
        const { author, social } = data.site.siteMetadata
        return (
          <div
            style={{
              display: `flex`,
              marginBottom: rhythm(2.5),
            }}
          >
            <Image
              fixed={data.avatar.childImageSharp.fixed}
              alt={author}
              style={{
                marginRight: rhythm(1 / 2),
                marginBottom: 0,
                minWidth: 50,
                borderRadius: `100%`,
              }}
            />
            <p>
              <strong>{author}</strong> <br/>Nació en Costa Rica el 4 de abril de 1947, e inició su ministerio en 1967. Se casó con Ana Delmy Ortiz en febrero de 1968, con quien engendró dos hijos. Junto con su esposa ha servido a Dios en Costa Rica, El Salvador, Guatemala y Estados Unidos; en el pastorado, la consejería, la enseñanza, talleres matrimoniales y pastorales, así como conferencias ministeriales. Actualmente escribe libros y desarrolla un ministerio que se enfoca en la renovación ministerial de los pastores, con el nombre de Ceremi Internacional.<br/>
              {` `}
              <a href={`https://www.facebook.com/${social.facebook}`}>
                Siguelo en Facebook
              </a>
            </p>
          </div>
        )
      }}
    />
  )
}

const bioQuery = graphql`
  query BioQuery {
    avatar: file(absolutePath: { regex: "/pastorOrlando.png/" }) {
      childImageSharp {
        fixed(width: 50, height: 50) {
          ...GatsbyImageSharpFixed
        }
      }
    }
    site {
      siteMetadata {
        author
        social {
          facebook
        }
      }
    }
  }
`

export default Bio
